
.main-footer {
  background: rgba(242, 242, 242, 1);
}

.main-footer__wrapper {
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-footer__logo {
  padding: 40px 0;
}

.main-footer__social {
  padding: 0 74.5px 40px 74.5px;
}

.main-footer__copyright {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 40px;

  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19.2px;
  color: rgba(68, 68, 68, 1);
}

@media (min-width: 769px) {
  .main-footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 55px 30px;
  }

  .main-footer__logo {
    padding: 0;
  }

  .main-footer__social {
    padding: 0;
  }
}

@media (min-width: 1440px) {
  .main-footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 66px 110px;
  }
}
